const string = `.cat {
    position: relative;
}

.head {
    position: relative;
    background: white;
    width: 400px;
    height: 352px;
    border: 2px solid black;
    left: 50%;
    top: 200px;
    margin-left: -200px;
    border-radius: 45% 45% 40% 40%/60% 60% 35% 35%;
    overflow: hidden;
}

.pattern {
    position: absolute;
    top: 50%;
    left: 50%;
    background-repeat: no-repeat;
}

.skin .pattern.left1 {
    background-image: url('./images/pattern-left1.jpg');
    transform: translate(-168px, 64px);
    width: 48px;
    height: 66px;
}

.skin .pattern.left2 {
    background-image: url('./images/pattern-left2.jpg');
    transform: translate(-198px, -20px);
    width: 34px;
    height: 84px;
}

.skin .pattern.right1 {
    background-image: url('./images/pattern-right1.jpg');
    transform: translate(95px, 72px);
    width: 72px;
    height: 56px;
}

.skin .pattern.right2 {
    background-image: url('./images/pattern-right2.jpg');
    transform: translate(156px, -20px);
    width: 34px;
    height: 86px;

}

.eye {
    width: 120px;
    height: 35px;
    border: 2px solid black;
    position: absolute;
    border-radius: 50% 50% 0% 0%/100% 100% 0% 0%;
    bottom: 98px;
    border-bottom: none;
    overflow: hidden;
}

.eye.left {
    left: 50px;
    transform: rotate(8deg);
}

.eye.right {
    right: 50px;
    transform: rotate(-8deg);

}

.eye.left::before {
    content: '';
    display: block;
    border: 2px solid black;
    position: absolute;
    top: 26px;
    left: 32px;
    width: 60px;
    height: 10px;
    border-color: black transparent transparent transparent;
    border-radius: 60% 60% 0% 0%/100% 100% 0% 0%;
}

.eye.left::after {
    content: '';
    display: block;
    border: 2px solid black;
    width: 38px;
    height: 36px;
    position: absolute;
    left: 42px;
    transform: translateY(-8px);
    background: black;
}

.eye.right::before {
    content: '';
    display: block;
    border: 2px solid black;
    position: absolute;
    top: 26px;
    left: 32px;
    width: 60px;
    height: 10px;
    border-color: black transparent transparent transparent;
    border-radius: 60% 60% 0% 0%/100% 100% 0% 0%;
}

.eye.right::after {
    content: '';
    display: block;
    border: 2px solid black;
    width: 38px;
    height: 36px;
    position: absolute;
    left: 42px;
    transform: translateY(-8px);
    background: black;
}

.lip {
    border: 2px solid black;
    width: 58px;
    height: 25px;
    position: absolute;
    top: 300px;
    border-radius: 72% 0% 100% 40%/60% 0% 100% 70%;
    border-color: transparent transparent black black;
}

.mouse .lip.left {
    left: 148px;
    transform: rotate(-6deg);
    animation: shakeForLeft 800ms infinite linear;
    transform-origin: 100% 100%;
}

.mouse .lip.right {
    right: 206px;
    transform-origin: 100% 0%;
    animation: shakeForRight 800ms infinite linear;
}

.noseWrapper:hover {
    transform-origin: 50% 100%;
    animation: shakeForNose 400ms 2 linear;
}

.nose {
    border: 2px solid black;
    position: relative;
    width: 24px;
    height: 20px;
    border-color: transparent transparent black transparent;

}

.nose.left {
    top: 286px;
    left: 180px;
    transform: rotate(26deg);
}

.nose.right {
    top: 266px;
    left: 192px;
    transform: rotate(-26deg) rotateY(180deg);
}

.ear {
    border: 2px solid black;
    position: absolute;
    top: 50%;
    left: 50%;
    border-radius: 3% 97% 85% 15% / 31% 42% 58% 69%;
    border-color: black transparent transparent black;
    background: white;
    width: 140px;
    height: 157px;
}

.ear.left {
    /* width: 147px;
    height: 177px; */
    transform: translate(-185px, 2px) rotate(6deg);
}

.ear.right {
    /* width: 160px;
    height: 177px; */
    transform: translate(64px, 18px) rotate(3deg) rotateY(180deg);
}

/* 头上的颜色 */
.head::before {
    content: '';
    display: block;
    /* border: 2px solid black; */
    width: 170px;
    height: 320px;
    position: absolute;
    top: -100px;
    left: 120px;
    background: rgb(244, 173, 107);
    background: linear-gradient(90deg,
            rgba(244, 173, 107, 1) 50%,
            rgba(122, 113, 108, 1) 50%);
    border-radius: 52% 45% 56% 60%/65% 60% 40% 40%;
    transform: rotate(5deg);
    z-index: 5;
}

.cat .catBody {
    position: absolute;
    width: 500px;
    height: 452px;
    border: 2px solid black;
    top: 50%;
    left: 50%;
    transform: translate(-250px, -64px);
    border-radius: 280px 280px 120px 120px / 350px 347px 169px 182px;
    background: rgb(211, 201, 191);
    overflow: hidden;
}

.cat .catBody::before {
    position: absolute;
    content: '';
    display: block;
    border-radius: 50%;
    width: 500px;
    height: 500px;
    background: rgb(244, 173, 107);
    transform: translate(-233px, -111px);
}

.cat .catBody::after {
    content: '';
    display: block;
    border-radius: 50%;
    width: 500px;
    height: 500px;
    background: rgb(122, 123, 118);
    position: absolute;
    transform: translate(255px, -111px);
}

.cat .bodySkin.center {
    position: absolute;
    height: 100px;
    width: 110px;
    background: rgb(244, 173, 107);
    top: 0;
    left: 50%;
    transform: translate(-46px, -11px);
    border-radius: 50%;
}

.cat .tail {
    border: 2px solid black;
    width: 110px;
    height: 110px;
    position: absolute;
    background: white;
    border-radius: 50%;
    top: 52px;
    left: 50%;
    margin-left: 42px;
    ;
}

.cat .tail:hover {
    transform-origin: 50% 100%;
    animation: wave 300ms infinite linear;
}`
let css1 = document.querySelector('.css1')
let css2 = document.querySelector('.css2')

let n = 0;
let id = setInterval(() => {
    n += 1
    if (n > string.length) {
        window.clearInterval(id)
        return
    }
    css1.innerText = string.substr(0, n)
    css2.innerHTML = string.substr(0, n)
    css1.scrollTop = css1.scrollHeight

}, 0)